@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Playfair+Display:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Playfair+Display:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.home__header{text-align:center}
.view-schedule-button{height:66px;width:335px;text-transform:uppercase;background-color:#3c3c3c;border-radius:5px;color:#fff;font-weight:700;font-family:"Poppins",sans-serif;font-size:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center;text-decoration:none;margin:auto}
.event-header-details{color:#fff;background-color:#fa825d;line-height:1.5rem;font-family:"PT Sans",sans-serif;padding:1.438rem}.event-header-details>*{margin:0}.event-header-details__divider{margin-left:.313rem;margin-right:.313rem}.event-header-details__venue{font-weight:bold}.event-header-details__location{font-weight:normal}
